<template>
  <div>
    <van-tabs v-model="tabIndex" @change="_tab_change">
        <van-tab
            v-for="(tab,index) in tabs"
            :key="index"
            :title="tab"
        ></van-tab>
    </van-tabs>
    
    <ul style="background-color: #fff;border-top: 1px solid #eee;">
        <li class="flex-between flex-vertical" style="border-bottom:1px solid #eee;">
            <span class="otc-item-field flex-center">姓名</span>
            <span class="otc-item-field flex-center">金额</span>
            <span class="otc-item-field flex-center">时间</span>
        </li>
        <li
            v-for="(OTC,OTCIndex) in OTCList"
            :key="OTCIndex"
            class="flex-between flex-vertical"
            style="border-bottom:1px solid #eee;color:#666;"
        >
            <span class="otc-item-field flex-center">{{OTC.userName}}</span>
            <span class="otc-item-field flex-center">{{OTC.otcMoney}}</span>
            <span class="otc-item-field flex-center">{{OTC.createTime}}</span>
        </li>
    </ul>
    <p v-if="OTCList.length === 0" class="flex-center" style="font-size:12px;color:#999;height:30px;">没有数据</p>
  </div>
</template>

<script>
import { getOTCList } from '@/api';
export default {
    data:()=>({
        tabs: ['红包卖出','红包买入','通证卖出','通证买入'],
        tabIndex: 0,
        OTCList: []
    }),
    computed: {
        otcType: function(){
            if(this.tabIndex === 0 || this.tabIndex === 1) return 1;
            else if(this.tabIndex === 2 || this.tabIndex === 3) return 2;
        },
        otcSaleorBuy: function(){
            if(this.tabIndex === 0 || this.tabIndex === 2) return 1;
            else if(this.tabIndex === 1 || this.tabIndex === 3) return 2;
        }
    },
    mounted(){
        this.getOTCList();
    },
    methods: {
        getOTCList(){
            let { otcType, otcSaleorBuy } = this;
            getOTCList({
                otcType,
                otcSaleorBuy,
                state: 0
            }).then(res=>{
                console.log(res);
                if(res.code === 0){
                    this.OTCList = res.data.list;
                }else{
                    this.$toast.fail(res.msg);
                }
            })
        },
        _tab_change(){
            this.getOTCList();
        }
    }
}
</script>

<style scoped>
    .otc-item-field{
        width: 33%;
        height: 50px;
        font-size: 14px;
        text-align: center;
    }
</style>